.controls{height:15%;display:flex;justify-content:space-around;align-items:center;font-family:"Share Tech Mono",monospace;white-space:nowrap;margin-top:5%;margin-bottom:1%}.controls .bet-one,.controls .draw,.controls .bet-max{height:75%;width:20%;border:3px solid white;border-radius:10px;background-color:#000;color:#fff;text-transform:uppercase;display:flex;justify-content:center;align-items:center;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-filter:drop-shadow(10px 5px 4px black);filter:drop-shadow(10px 5px 4px black)}

.cards{background-color:#1bad1b;display:flex;justify-content:space-evenly;align-items:center;margin-bottom:-10%;aspect-ratio:1.9/ 1}.cards .playing-cards{width:17%;height:65%}

.hold-buttons{height:30%;display:flex;justify-content:space-around;align-items:center}.hold-buttons button{width:18%;height:60%}.hold-buttons .hold-button{border:2px solid black;font-family:"Share Tech Mono",monospace;font-size:1.5rem;width:11%;height:content;-webkit-user-select:none;-ms-user-select:none;user-select:none;display:flex;justify-content:center;align-items:center}.hold-buttons .light-red{background-color:#ffe7aaf3}.hold-buttons .dark-red{background-color:#cf0303}

.table{display:flex;flex-direction:column}.table .cards,.table .hold-buttons,.table .controls{width:100%}

.credits{width:100%;height:100%;display:flex;flex-direction:column;justify-content:space-around;color:#fff;font-family:"Share Tech Mono",monospace}.credits .credits-wrapper,.credits .current-bet,.credits .credit-controls{display:flex;justify-content:center}.credits .credits-wrapper{font-size:1.3rem}.credits .display-credits{margin-left:0.5rem}.credits .minus,.credits .plus{border:3px solid white;border-radius:10px;background-color:#000;padding:0.7em 1.3em;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-filter:drop-shadow(10px 5px 4px black);filter:drop-shadow(10px 5px 4px black)}.credits .minus:hover,.credits .plus:hover{cursor:pointer}.credits .bet-amount,.credits .current-bet{font-size:1.3rem;align-self:center;margin:0 1.5rem}.credits .bet-amount{margin-left:1.5em}.credits .current-bet{white-space:nowrap}

.outcome{font-family:"Share Tech Mono",monospace;font-size:3rem;color:#fff;padding-top:3rem;text-align:center}

.pay{font-family:"Share Tech Mono",monospace;font-size:0.8rem;text-align:center;color:#fff;padding:1.5rem 0}table{font-weight:100}.col{margin:8px;padding:6px 6px}.head{margin-left:10px}.active{background:greenyellow;color:black}.paytable{margin:1rem 0}

.odds{font-family:"Share Tech Mono",monospace;font-weight:bold;background-color:#1bad1b;display:flex;flex-direction:column;justify-content:space-between;text-align:center;color:#fff;padding:2rem}

.wrapper{display:grid;grid-template-columns:1fr 3fr 1.1fr;grid-template-rows:2fr 1fr;width:100vw;height:100vh}.odds{grid-area:1 / 1 / 2 / 2}.table{grid-area:1 / 2 / 2 / 3}.pay{grid-area:1 / 3 / 2 / 4}.credits{grid-area:2 / 1 / 3 / 2}.outcome{grid-area:2 / 2 / 3 / 3}.hints{grid-area:2 / 3 / 3 / 4}.odds,.table,.pay,.credits,.outcome,.hints{background:#1bad1b}@media (max-width: 700px){.wrapper{grid-template-columns:1fr;grid-template-rows:repeat(6, 1fr)}.table{grid-area:1 / 1 / 2 / 2}.credits{grid-area:2 / 1 / 3 / 2}.outcome{grid-area:3 / 1 / 4 / 2}.pay{grid-area:4 / 1 / 5 / 2}.odds{grid-area:5 / 1 / 6 / 2}.hints{grid-area:6 / 1 / 7 / 2}}

*{box-sizing:border-box;padding:0;margin:0}

